.geomarket-map_container {
  padding: 3.2rem 0.8rem;
  position: relative;
  display: inline-block;
}
.geomarket-map_container img {
  width: 100%;
}
.geomarket-map_container .point {
  cursor: pointer;
  position: absolute;
  width: 1rem;
  height: 1rem;
  background-color: #ff7300;
  border: 1px solid white;
  border-radius: 50%;
  transition: all 0.3s ease;
  will-change: transform, box-shadow;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 0 rgba(255, 115, 0, 0.4);
  animation: pulse 3s infinite;
}
.geomarket-map_container .point:hover {
  animation: none;
  transform: translate(-50%, -50%) scale3D(1.35, 1.35, 1);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}
.geomarket-map_container .point:hover .data-tooltip {
  opacity: 1;
  pointer-events: auto;
  left: 50%;
  -webkit-transform: translateX(-47%);
  -moz-transform: translateX(-47%);
  -ms-transform: translateX(-47%);
  -o-transform: translateX(-47%);
  transform: translateX(-47%);
}
.geomarket-map_container .point .data-tooltip {
  background: #383f4a;
  bottom: 100%;
  color: #fff;
  display: block;
  font-size: 0.875rem;
  left: -28px;
  margin-bottom: 15px;
  opacity: 0;
  padding: 0.55rem 1rem;
  pointer-events: none;
  position: absolute;
  min-width: 100%;
  -webkit-transform: translateY(10px);
  -moz-transform: translateY(10px);
  -ms-transform: translateY(10px);
  -o-transform: translateY(10px);
  transform: translateY(10px);
  -webkit-transition: all 0.25s ease-out;
  -moz-transition: all 0.25s ease-out;
  -ms-transition: all 0.25s ease-out;
  -o-transition: all 0.25s ease-out;
  transition: all 0.25s ease-out;
  -webkit-box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.28);
  -moz-box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.28);
  -ms-box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.28);
  -o-box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.28);
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.28);
  /* This bridges the gap so you can mouse into the data-tooltip without it disappearing */
  /* CSS Triangles - see Trevor's post */
}
.geomarket-map_container .point .data-tooltip:before {
  bottom: -20px;
  content: " ";
  display: block;
  height: 20px;
  left: 0;
  position: absolute;
  width: 100%;
}
.geomarket-map_container .point .data-tooltip:after {
  border-left: solid transparent 10px;
  border-right: solid transparent 10px;
  border-top: solid #383f4a 10px;
  bottom: -10px;
  content: " ";
  height: 0;
  left: 50%;
  margin-left: -13px;
  position: absolute;
  width: 0;
}
