@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(254, 252, 242, 0.5);
  }
  70% {
    box-shadow: 0 0 0 25px rgba(254, 252, 242, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(254, 252, 242, 0);
  }
}
/* IE can just show/hide with no transition */
.lte8 .point .data-tooltip {
  display: none;
}

.lte8 .point:hover .data-tooltip {
  display: block;
}